
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #272822;
  margin-top: 60px;
  font-size: 1em;
}
.container {
  margin-bottom: 2%;
}
.ui.segment, .ui.button.icon {
  font-size: 2em;
}
.ui.button {
  font-size: 1em;
}
.ui.form .field>label, .ui.form .field>input, .ui.button.createBtn, .ui.button.cancelBtn, .ui.button.closeBtn {
  font-size: 2em;
}
.ui.form .field>input {
  font-size: 1.5em;
}
.header {
  font-weight: 700;
  overflow-wrap: break-word;
  line-height: initial;
}
.header, .meta, .ui.form .field>label, .ui.form .field>input, .ui.button.createBtn, .icons {
  margin-bottom: 1em;
}
.icons {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.ui.button {
  background: none;
  margin-right: 0px;
  margin-left: 0px;
  width: 100%;
}
.date {
  font-size: 0.65em;
}
